<template>
  <div>
      <h4>{{title}}</h4>
      <chrome-picker v-model="color" @input="updateValue"></chrome-picker>
  </div>
</template>

<script>
import { Chrome } from 'vue-color';

  export default {
    components: {
        'chrome-picker': Chrome,
    },
    data: function() {
        return {
            color: 'white'
        }
    },
    methods: {
      updateValue: function() {
        this.onUpdate(this.color.hex);
      }
    },
    name: 'ColourPicker',
    props: {
        onUpdate: Function,
        title: String
    }
  }
</script>

