<template>
  <v-container>
      <h3>Change Theme</h3>
      <v-row>
        <v-btn-toggle rounded>
          <v-btn @click="setLightTheme">Light</v-btn>
          <v-btn @click="setDarkTheme">Dark</v-btn>
        </v-btn-toggle>
      </v-row>
      <h3>Change Colours</h3>
      <v-row>
        <v-spacer></v-spacer>
        <colour-picker :on-update="updateTextColour" :title="'Change Text Colour'"></colour-picker>
        <v-spacer></v-spacer>
        <colour-picker :on-update="updateCardColour" :title="'Change Card Colour'"></colour-picker>
        <v-spacer></v-spacer>
      </v-row>
  </v-container>
</template>

<script>
import ColourPicker from './ColourPicker';

  /**
   * Sets the colour of the property within the current theme.
   */
  function _setColour(color, property) {
    let theme = this.$vuetify.theme.isDark ? 'dark' : 'light';
    this.$vuetify.theme.themes[theme][property] = color;
  }

  export default {
    components: {
      'colour-picker': ColourPicker,
    },
    data: function() {
      return {
      }
    },
    methods: {
      setDarkTheme: function() {
        this.$vuetify.theme.dark = true;
      },
      setLightTheme: function() {
        this.$vuetify.theme.dark = false;
      },
      updateCardColour: function(color) {
        _setColour.call(this, color, 'card');
      },
      updateTextColour: function(color) {
        _setColour.call(this, color, 'text');
      },
    },
    name: 'MainContainer'
  }
</script>

